<template>
    <div>
        <TopNav title="添加拼团"></TopNav>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <div class="wyu-tips wyu-tips-info" style="margin-bottom:10px;">
                    <p class="tit">1)拼团产品包邮以 关联产品为准</p>
                </div>
                <el-form v-if="active == 1"  :model="goodsDetail" ref="goodsInfo" size="mini"
                         label-width="100px">
                    <div class="wyu-title">
                        <h2>选择产品</h2>
                    </div>
                    <el-form-item label="选择产品" >
                        <product-select-spec @selGoods="getGoods" :prom_type='0'></product-select-spec>
                    </el-form-item>
                    <el-form-item label="PLUS会员特价">
                        <el-switch v-model="goodsDetail.is_vip" :active-value="1" :inactive-value="0"></el-switch>
                    </el-form-item>
                    <el-form-item label="产品名称 " v-if="goods">
                        {{goods.goods_name  }}
                    </el-form-item>
                    <el-form-item label="产品图片" v-if="goods">
                        <img :src="goods.photo" width="100"/>
                    </el-form-item>
                    <el-form-item label="选择规格"  v-if="goods">
                        <div v-if="goods.sku_type == 0">
                            价格：{{goods.shop_price}}
                        </div>
                        <template v-if="goods.sku_type == 1">
                            <el-table size="mini" :data="goods.goods_spec" row-key="spec_key" @selection-change="seleSpec"    ref="multipleTable"  height="250"  border style="width: 100%">
                                    <el-table-column :reserve-selection="true" prop="spec_key" type="selection" width="55"></el-table-column>

                                    <el-table-column label="规格项" prop="key_name"></el-table-column>

                                    <el-table-column label="价格" prop="shop_price" width="100"></el-table-column>

                                    <el-table-column label="团购价" width="150">
                                        <template slot-scope="scope">
                                            <el-input v-model="scope.row.groupbuy_price"></el-input>
                                        </template>
                                    </el-table-column>

                                    <el-table-column label="PLUS会员团购价" width="150" v-if="goodsDetail.is_vip">
                                        <template slot-scope="scope">
                                            <el-input v-model="scope.row.vip_groupbuy_price"></el-input>
                                        </template>
                                    </el-table-column>

                                    <el-table-column label="库存" prop="goods_num" width="100"></el-table-column>
                            </el-table>
                        </template>
                    </el-form-item>
                    <div class="wyu-title">
                        <h2>基本信息</h2>
                    </div>
                    <el-form-item label="团购价" v-if="goods.sku_type == 0">
                        <el-input v-model="goodsDetail.groupbuy_price" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">团购价格</p>
                    </el-form-item>

                    <el-form-item label="会员团购价" v-if="goods.sku_type == 0 && goodsDetail.is_vip == 1">
                        <el-input v-model="goodsDetail.vip_groupbuy_price" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">PLUS会员团购价格</p>
                    </el-form-item>

                    <el-form-item label="需要人数">
                        <el-input v-model="goodsDetail.need_num" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">指定多少人数才能成团 (2-5人)</p>
                    </el-form-item>

                    <el-form-item label="拼团有效期">
                        <el-input v-model="goodsDetail.end_time" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">参与拼团后 多少小时后未拼团成功 即为拼团失败 单位（小时）</p>
                    </el-form-item>

                    <!--<el-form-item label="剩余数量">-->
                        <!--<el-input v-model="goodsDetail.num" style="width: 500px;"></el-input>-->
                        <!--<p class="wyu-form-tips">拼团成功次数限制 请以实际库存为准 如：100 则表示 拼团成功 100 次 则下架 </p>-->
                    <!--</el-form-item>-->

                    <el-form-item label="排序">
                        <el-input v-model="goodsDetail.orderby" style="width: 500px;"></el-input>
                    </el-form-item>

                    <!--<el-form-item label="已成功数量">-->
                        <!--<el-input v-model="goodsDetail.success_num" style="width: 500px;"></el-input>-->
                        <!--<p class="wyu-form-tips">已成功拼团人数</p>-->
                    <!--</el-form-item>-->

                    <el-form-item label="是否开启">
                        <el-switch v-model="goodsDetail.is_online" :active-value="1" :inactive-value="0"></el-switch>
                    </el-form-item>


                    <div class="wyu-title">
                        <h2>SEO设置</h2>
                    </div>
                    <el-form-item label="百度SEO标题" label-width="120px">
                        <el-input v-model="goodsDetail.seo.title" style="width:500px;"  autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="百度SEO描述" label-width="120px">
                        <el-input type="textarea" :autosize="{minRows: 2, maxRows: 5}" style="width:500px;" autocomplete="off"  placeholder="请输入内容" v-model="goodsDetail.seo.keywords"></el-input>
                    </el-form-item>
                    <el-form-item label="百度SEO关键词" label-width="120px">
                        <el-input type="textarea" :autosize="{minRows: 5, maxRows: 10}" style="width:500px;" autocomplete="off"  placeholder="请输入内容" v-model="goodsDetail.seo.description"></el-input>
                    </el-form-item>
                    <el-form-item label="百度SEO原始发布时间" label-width="120px">
                        <el-date-picker v-model="goodsDetail.seo.releaseDate" type="date" placeholder="选择日期"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="百度封面图片" label-width="120px">
                        <img-uploads :multi="true" :img="goodsDetail.seo.image"  v-model="goodsDetail.seo.image"></img-uploads>
                        <p class="tit"> 用于信息流投放后的封面显示，最多3张，单图片最大2M；封面图建议尺寸：高>=210px 宽>=375px;最小尺寸: 高>=146px 宽>=218px</p>
                    </el-form-item>

                </el-form>
            </div>
        </div>
        <div class="wyu-btn-div">
            <el-button size="small" type="primary"  @click="createGoods()">创建拼团</el-button>
            <router-link to="/marketing/groupbuy/index" style="margin-left: 10px">
                <el-button size="small">取消</el-button>
            </router-link>
        </div>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import ProductSelectSpec from "../../product/components/ProductSelectSpec";
    import ImgUploads from "../../../../../sqtuan/src/components/Photos";
    export default {
        name: 'BargainCreate',
        components: {
            ImgUploads,
            ProductSelectSpec,
            TopNav
        },
        data() {
            return {
                loading: false,
                active: 1,
                typeId: 0,
                data: {},
                goods: {},
                goodsDetail: {
                    goods_id:0,
                    spec:[],
                    num:0,
                    groupbuy_price:0,
                    vip_groupbuy_price:0,
                    is_online:1,
                    end_time:0,
                    orderby:0,
                    success_num:0,
                    need_num:0,
                    is_vip: 1,
                    seo: {
                        title: '',
                        keywords: '',
                        description: '',
                        releaseDate: '',
                        image: [],
                    },
                },

            }
        },
        created() {

        },

        methods: {

            getGoodsData(){
                let data = [];
                this.goods.goods_spec.forEach(v => {
                    if(v.goods_num > 0){
                        data.push(v);
                    }
                });
                this.data = data;
                return data;
            },

            getGoods(val) {
                this.goods = val;
                if(this.$refs.multipleTable != undefined) {
                    console.log(this.$refs.multipleTable);
                    this.$refs.multipleTable.clearSelection();
                }
                this.goodsDetail.spec = val.goods_spec;
            },

            seleSpec(val){
                let spec = [];
                val.forEach( v => {
                    spec.push(v);
                });
                console.log(spec);
                this.goodsDetail.spec = spec;
            },

            //提交商品到商品库
            createGoods() {
                this.loading = true;

                api.post("/shop/admin.marketing.groupbuy/create", {
                    appToken: this.$store.getters.appToken,
                    goods_id: this.goods.goods_id,
                    spec: JSON.stringify(this.goodsDetail.spec),
                    //num: this.goodsDetail.num,
                    is_online: this.goodsDetail.is_online,
                    end_time: this.goodsDetail.end_time,
                    orderby: this.goodsDetail.orderby,
                    //success_num: this.goodsDetail.success_num,
                    need_num: this.goodsDetail.need_num,
                    is_vip: this.goodsDetail.is_vip,
                    groupbuy_price: this.goodsDetail.groupbuy_price,
                    vip_groupbuy_price: this.goodsDetail.vip_groupbuy_price,
                    seo: JSON.stringify(this.goodsDetail.seo),
                }).then(res => {
                    this.loading = false;
                    this.$message({
                        message: '提交成功',
                        type: 'success'
                    });
                    setTimeout(() => {
                        this.$router.push("/marketing/groupbuy/index");
                    }, 1000);
                }).catch((error) => {
                    this.loading = false;
                });
            },
        }
    }
</script>
